@import '../../../assets/styles/mixins/flex.scss';
@import '../../../assets/styles/typography.scss';

.request-join {
    &__section {
        background: #EEF3F9;
        position: relative;
    }

    &__title {
        margin-bottom: 20px;
        font-size: 36px;
    }

    &__title-container {
        @include flex(column, flex-start, flex-start, nowrap);
    }
    
    &__container {
        position: relative;
        @include flex(row, space-between, flex-start, nowrap);
        padding-bottom: 200px;
        padding-top: 100px;
        font-family: var(--font-family-segoe);
        z-index: 1;
    }

    &__description {
        font-family: var(--font-family-uni-sans);
        font-size: 16px;
        max-width: 337px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__docs {
        @include flex(row, space-between, flex-start, nowrap);
        margin-right: -15px;
    }

    &__img {
        margin-bottom: 15px;
    }

    &__yellow {
        visibility: hidden;
        padding-left: 15px;
    }

    &__text {
        font-family: var(--font-family-uni-sans);
        font-size: 16px;
        max-width: 255px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin-bottom: 24px;
    }

    &__link {
        color: #0063E7;
        font-weight: 700;
        transition: .2s;

        &:hover {
            opacity: .3;
        }
    }

    &__rectangle {
        transform: rotate(-5deg);
        position: absolute;
        bottom: -150px;
        width: 105%;
        height: 315px;
        background: white;
    }
}